.definition{
  display: flex;
  gap: 2rem;
  padding: 5rem 0rem;
  min-height: 77vh;
}
.left-dif{
  display: flex;
  flex-direction: column;
  gap: 7rem;
  flex: 1;
}

.dif-text>:first-child{
  font-size: 2rem;
  margin-bottom: .8rem;
}
.dif-text>:nth-child(2){
  font-size: 3rem;
  margin-bottom: .8rem;
}
.dif-text>:nth-child(3){
  font-size: 1.3rem;
  margin-bottom: .8rem;
}
.social{
  display: flex;
  align-items: center;
  gap: 1rem;
}
.social >img{
  width: 2rem;
  height: 2rem;
}

.right-dif{
  flex: 1;
  position: relative;
}
.right-dif >img:first-child{
  position: absolute;
  left: 9rem;
  z-index: 5;
}
.right-dif >img:nth-child(2){
  width: 5rem;
  position: absolute;
  top: 3px;
  left: 7rem;
}
.right-dif >div:first-of-type{
  position: absolute;
  display: flex;
  align-items: center;
  gap: 1rem;
  position: absolute;
  top: 20rem;
  left: 2rem;
  background-color: white;
  padding: 8px 10px;
  border-radius: 15px;
  text-transform: capitalize;
}
.right-dif >div:first-of-type>img{
  width: 3rem;
}
.right-dif >img:nth-of-type(3),.right-dif >img:nth-of-type(4){
  width: 25rem;
  position: absolute;
  top: 3rem;
  left: 5rem;
}
.right-dif >div:last-of-type>img{
  width: 3rem;
}
.right-dif >div:last-of-type{
  text-transform: capitalize;
  position: absolute;
  display: flex;
  align-items: center;
  gap: 1rem;
  position: absolute;
  top: 3rem;
  left: 20rem;
  background-color: white;
  padding: 8px 10px;
  border-radius: 15px;
}
@media (max-width:767px) {
  .definition{
    flex-direction: column;
    margin-bottom: 15rem;
  }
  .right-dif > img:first-child {
    left: 0;
    width: 15rem;
  }
  .right-dif > img:nth-of-type(3), .right-dif > img:nth-of-type(4) {
    width: 15rem;
    top: 8rem;
    left: 37px;
  }
  .right-dif > img:nth-child(2) {
    left: 0;
  }
  .right-dif > div:last-of-type {
    left: 7rem;
  }
}