.brand{
  display: flex;
  gap: 6rem;
}
.left-brand{
  display: flex;
  flex-direction: column;
  gap: 4rem;
  flex: 1;
}
.brand-text>div{
font-size: 35px;
display: flex;
flex-direction: column;
margin-bottom: 15px;
font-weight: bold;
text-transform: uppercase;
}
.brand-text>div>span:last-child{
  color: var(--orange);
  font-size: 45px;
}
.brand-text p{
  font-size: 1.3rem;
}
.right-brand{
  position: relative;
  flex: 1;
}
.right-brand .all-brands{
position: relative;
background-color: white;
width: 26rem;
height: 26rem;
border-radius: 50%;
z-index: 55;
}
.right-brand .all-brands>img{
  position: absolute;
  width: 5rem;
}
.right-brand .all-brands>img:first-child{
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
  background-color: aliceblue;
  border-radius: 50%;
  padding: 5px;
}
.right-brand .all-brands>img:nth-child(2){
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  background-color: aliceblue;
  border-radius: 50%;
  padding: 5px;
}
.right-brand .all-brands>img:nth-child(3){
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: aliceblue;
  border-radius: 50%;
  padding: 5px;
}
.right-brand .all-brands>img:nth-child(4){
  top: 50%;
  left: 0%;
  transform: translatey(-50%);
  background-color: aliceblue;
  border-radius: 50%;
  padding: 5px;
}
.right-brand .all-brands>img:nth-child(5){
  top: 50%;
  right: 0%;
  transform: translatey(-50%);
  background-color: aliceblue;
  border-radius: 50%;
  padding: 5px;
}
