.right-brand>img:first-child{
  background: #1949b8;
  left: 18rem;
  top: 0rem;
}
.right-brand>img{
  position: absolute;
  height: 13rem;
  width: 13rem;
  border-radius: 100%;
  /* darkMode */
  z-index: 1;
  top: 20px;
  right: 200px;
}
.right-brand>img:last-child{
  background: #f5c32c;
  left: 18rem;
  top: 8rem;
}
@media (max-width:992px) {
  .brand{
    flex-direction: column;
    align-items: center;
  }
  .right-brand .all-brands {
    width: 20rem;
    height: 20rem;
  }
  .right-brand>img{
    position: absolute;
    width: 10rem;
    height: 18rem;
    top: 40px;
    left: 10rem;
  }
  .right-brand>img:first-of-type{
    top: 10px;
    left: 15rem;
    transform: rotate(-40deg);
  }
  .right-brand>img:last-of-type{
    top: 80px;
    left: 15rem;
  }
}