.number{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
}
.number .num{
  background-color: transparent;
  border-style: solid;
  border-width: 5px;
  border-color: red;
  width: 80px;
  height: 80px;
  font-size: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.type{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
}
.type>span:last-child{
  color: var(--orange);
}