.portfolio{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  height: 70vh;
  justify-content: center;
  padding: 2rem 0rem;
  overflow: hidden;
}
.portfolio-header{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.portfolio-header>span{
  font-size: 30px;
  font-weight: 500;
}
.portfolio .swiper{
  overflow: hidden;
  width: 100%;
  display: flex;
  align-items: center;
}
.swiper-wrapper{
  display: flex;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  position: relative;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  flex-direction: column;
  border-radius: 25px;
  width: 20rem;
  border-radius: 25px;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 25px;
}