.Footer{
  display: flex;
  align-items: center;
  margin: -0.5rem -3.5rem;
  position: relative;
  margin-top: -7rem;
  padding-top: 5rem;
}
.footer-content{
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin-top: 8rem;
  gap: 4rem;
  color: white;
}
.footer-content img{
  width: 4rem;
}
@media screen and (max-width: 480px){
  .footer-content{
    transform: scale(.5);
  }
}
@media (max-width:767px) {
  .footer-content{
    margin-top: 4rem;
    font-size: x-large;
    text-transform: capitalize;
    gap: 1rem;
  }
}