.contact{
  padding: 3rem 0rem;
  display: flex;
  gap: 3rem;
  flex-wrap: wrap;
}
.left-contact{
  flex: 1;
}
.right-contact{
  flex: 1;
}
.left-contact>div{
display: flex;
flex-direction: column;
align-items: center;
font-size: 30px;
text-transform: capitalize;
font-weight: 500;
}
.right-contact form{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
.right-contact form input:nth-of-type(1),
.right-contact form input:nth-of-type(2)
{
  width: 100%;
  height: 40px;
  padding: 0px 5px;
  border-radius: 8px;
  border: 3px solid var(--orange);
}
.right-contact form textarea{
  width: 100%;
  padding: 5px;
  border: 3px solid var(--orange);
}
form >*{
  outline: none;
}
@media (max-width:767px) {
  .contact{
    flex-direction: column;
  }
}