  display: flex;
  padding: 5rem 0rem;
  height: 60rem;
}
.left-awe{
flex: 1 1;
display: flex;
flex-direction: column;
gap: 3rem;
}
.awe-text>div:first-child{
font-size: 25px;
text-transform: capitalize;
margin-bottom: 15px;
}
.right-awe{
flex: 2 2;
position: relative;
}
