 .imo{
  position: absolute;
  background-color: white;
  border: 3px solid var(--orange);
  text-align: center;
  padding: 10px 0px;
  border-radius: 25px;
 }
 .imo > :nth-child(1) {
  color: var(--black);
  font-size: 2.5rem;
  font-weight: bold;
}
.imo > :nth-child(2) {
  color: var(--orange);
  font-size: 2.5rem;
  font-weight: bold;
}
.imo > :nth-child(3) {
  color: var(--gray);
  font-size: 14px;
  margin-top: 1rem;
}
@media (max-width:992px) {
  .awesome{
    flex-direction: column;
    gap: 1.5rem;
    margin-bottom: 30rem;
  }
  .left-awe{
    width: 100%;
    align-items: center;
  }
  .right-awe{
    width: 100%;
  }
  .awe-text>div{
    text-align: center;
  }
  .awe-text p{
    text-align: center;
  }
  .right-awe .imo{
    padding: 15px;
    width: 20rem;
  }
  .right-awe > div:nth-child(3) {
    top: 15rem;
    left: -6px;
  }
}
@media (max-width:767px) {
  .right-awe .imo{
    position: relative;
  }
  .right-awe>div:first-child{
    top: 20px;
    right: 0px;
    }
    .right-awe>div:nth-child(3){
    top: 50px;
    right: 0px;
    }
    .right-awe>div:nth-child(2){
    top: 40px;
    right: 0px;
    }
}