.header{
  top: 0;
  width: 100%;
  z-index: 55555;
  padding: 0px;
position: fixed;
padding: 0rem 4rem ;
}

.header-content{
  padding: 1.5rem 0rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-content .left-cont{
  display: flex;
  align-items: center;
  gap: 1rem;
}
.right-cont{
  display: flex;
  align-items: center;
  gap: 2rem;
}
.links{
  list-style: none;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.links >li{
  text-transform: capitalize;
  font-size: 1.2rem;
  cursor: pointer;
}
@media (max-width:767px){
  .header{
    padding: 0;
  }
  .links{
    list-style: none;
    top: 66px;
    position: absolute;
    right: 0;
    background-color: red;
    padding: 10px;
    width: 100%;
    flex-direction: column;
  }
  .logo{
    font-size: 18px;
  }
}